    }

    .panel.image-compare.red div.jx-arrow.jx-left {
        border-color: transparent #B1040E transparent transparent;
    }

    .panel.image-compare.red div.jx-arrow.jx-right {
        border-color: transparent transparent transparent #B1040E;
    }

