    .panel.video-preview .video-container {
        position: relative;
        padding: 0;
    }

    .panel.video-preview .video-container.playing:after {
        content: " ";
        width: 100%;
        height: 100%;
        min-height: 30%;
        background: #000000;
        transition: all 0.25s ease-out;
        z-index: 2;
    }

    .panel.video-preview .video-container:before {
        display: block;
        content: "";
        width: 100%;
        padding-top: 56.25%;
    }

    .panel.video-preview .video-container:after {
        transition: all 0.25s ease-out;
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: traslateX(-50%) translateY(-50%);
        transform: translateX(-50%) translateY(-50%);
        content: " ";
        width: 100%;
        height: 100%;
        min-height: 30%;
        z-index: 2;
    }

    .panel.video-preview .video-container video,
    .panel.video-preview .video-container .overlay-content {
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: traslateX(-50%) translateY(-50%);
        transform: translateX(-50%) translateY(-50%);
        max-width: 100%;
        max-height: 100%;
    }

    .panel.video-preview .video-container iframe {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        z-index: 4;
